*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  height: 100%;
}

body {
  background-color: $color-primary;
  font-family: "Work Sans", Arial, sans-serif;
  font-size: $m-size;
  height: 100%;
}

#root {
  height: 100%;
}

